.hero {
  position: relative;

  /* Content */
  .hero__content {
    position: relative;
    z-index: 1;
    padding-top: 3rem;
    padding-bottom: 3rem;
    margin-bottom: 3rem;
  }

  /* Background */
  .hero__background {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    min-height: 100%;
  }

  .hero__background__container {
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    /* background-color: powderblue; */
  }

  .hero__background__one {

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center top;
    }
  }
}